import React, { useState, cloneElement, useEffect } from 'react';

let sliderTimerId = null;

const Slider = ({ children }) => {
  let [slide, setSlide] = useState(0);

  let items = React.Children.toArray(children);

  items = items
    .filter((item) => item.type && item.type.displayName === 'SliderItem')
    .map((item, index) => cloneElement(item, { isActive: index === slide }));

  const itemsCount = items.length;

  useEffect(() => {
    sliderTimerId = setInterval(() => {
      setSlide((prev) => (++prev > itemsCount - 1 ? 0 : prev));
    }, 5000);
    return () => clearInterval(sliderTimerId);
  }, [slide, itemsCount]);

  return (
    <div className='overflow-hidden' style={{ width: '50%' }}>
      <div
        className='flex transition-transform ease-linear'
        style={{
          width: `${100 * items.length}%`,
          transform: `translateX(-${(100 / items.length) * slide}%)`,
          transitionDuration: '700ms',
        }}
      >
        {items.map((item, index) => (
          <div style={{ width: `${100 / items.length}%` }} key={index}>
            {item}
          </div>
        ))}
      </div>
      <div>
        <button
          onClick={() => setSlide(--slide === -1 ? itemsCount - 1 : slide)}
        >
          prev
        </button>
        <button
          onClick={() => setSlide(++slide === itemsCount ? 0 : slide)}
          className='ml-10'
        >
          next
        </button>
      </div>
      <div className='mx-auto flex justify-center'>
        {Array(itemsCount)
          .fill()
          .map((item, index) => (
            <div
              onClick={() => setSlide(index)}
              className={`rounded-full w-3 h-3 bg-gray-400 pointer ml-1 cursor-pointer ${
                slide === index ? 'bg-gray-700' : 'bg-gray-400'
              }`}
            ></div>
          ))}
      </div>
    </div>
  );
};

export default Slider;
