import { Children, useState, cloneElement } from 'react';

const VAccordion = ({ children, multiple, duration, ...props }) => {
  //   console.log(multiple);
  const [active, setActive] = useState([]);

  let onActiveChange = (index) => {
    setActive(
      active.includes(index)
        ? active.filter((item, _) => item !== index)
        : multiple
        ? [...active, index]
        : [index]
    );
  };

  const items = Children.toArray(children)
    .filter((item) => item.type.displayName === 'VAccordionItem')
    .map((item, index) =>
      cloneElement(item, {
        onActiveChange: () => onActiveChange(index),
        isActive: active.includes(index),
        duration,
      })
    );

  //   console.log(children);

  return <ul {...props}>{items}</ul>;
};

export default VAccordion;
