import React, { useRef, useEffect, useState } from 'react';

// import { Transition } from 'react-transition-group';

// const defaultStyle = {};

// const transitionStyles = {
//   entering: { maxHeight: '24px' },
//   entered: { maxHeight: '24px' },
//   exiting: { maxHeight: '0px' },
//   exited: { maxHeight: '0px' },
// };
// ;
const VAccordionItemBody = ({ children, isActive, className, duration }) => {
  const [maxH, setMaxH] = useState('100%');
  // console.log(maxH);
  // console.log(duration);
  const ref = useRef(null);
  // let m = '100%';9999
  useEffect(() => {
    // m = `${ref.current.scrollHeight}px`;m
    setMaxH(`${ref.current.scrollHeight}px`);
  }, []);
  return (
    <div
      ref={ref}
      // style={{ transitionDuration: `${duration}ms` }}
      className={`transition-all ease-linear overflow-hidden ${className}`}
      style={{
        maxHeight: isActive ? maxH : '0px',
        transitionDuration: `${duration}ms`,
      }}
    >
      {children}
    </div>
  );
};
// ${isActive ? 'visible' : 'hidden'}
VAccordionItemBody.displayName = 'VAccordionItemBody';

export default VAccordionItemBody;
