import React, { useEffect, useState, memo } from 'react';

let AccordionItemTimerId = null;

let AccordionItem = ({
  children: Component,
  isActive,
  transitionDuration,
  ...props
}) => {
  const [done, setDone] = useState(false);

  useEffect(() => {
    if (isActive) {
      AccordionItemTimerId = setTimeout(() => {
        setDone(true);
      }, parseFloat(transitionDuration));
      return () => {
        setDone(false);
        clearTimeout(AccordionItemTimerId);
      };
    }
  }, [isActive, transitionDuration]);
  return (
    <Component
      isActive={isActive}
      transitionDuration={transitionDuration}
      done={done}
      {...props}
    />
  );
};

AccordionItem = memo(AccordionItem);

AccordionItem.displayName = 'AccordionItem';

export default AccordionItem;
