import { Children, cloneElement } from 'react';

const VAccordionItem = ({
  children,
  onActiveChange,
  isActive,
  duration,
  className = '',
  ...props
}) => {
  const items = Children.toArray(children)
    .filter(
      (item) =>
        (item.type && item.type.displayName === 'VAccordionItemBody') ||
        (item.type && item.type.displayName === 'VAccodionItemHeader')
    )
    .map((item, index) =>
      cloneElement(item, {
        onActiveChange,
        isActive,
        duration,
      })
    );

  return (
    <li className={`${className}`} {...props}>
      {items}
    </li>
  );
};

VAccordionItem.displayName = 'VAccordionItem';

export default VAccordionItem;
